.page {
  width: 80%;
}
@media only screen and (max-width: 800px) {
  .page {
    width: auto;
    max-width: 640px;
  }
}
@media only screen and (max-width: 800px) {
  .sort {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .buttons {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
/* the entry has the image and text split */
#projectsGoHere {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}
.content {
  position: relative;
  width: 350px;
  overflow: hidden;
  height: 400px;
  text-align: center;
  margin-top: 30px;
  margin: 10px 10px;
  background-color: rgba(0, 0, 0, 0.5);
}
.content .content-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.2;
  transition: all 0.3s ease-in-out 0s;
  z-index: 2;
}
.content .content-overlay:hover {
  opacity: 0;
}
.content-image {
  height: 300px;
  opacity: 1;
  transition: 0.3s;
  margin-left: 50%;
  transform: translateX(-50%);
}
.content-details {
  position: absolute;
  padding-left: 5em;
  padding-right: 5em;
  width: 90%;
  transform: translate(-50%, 100%);
  z-index: 3;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.content-details {
  top: 50%;
  left: 50%;
}
.content-details h3 {
  font-weight: 500;
  letter-spacing: 0.05em;
  margin-bottom: 0.5em;
  margin-top: 10px;
  text-transform: uppercase;
  color: black;
}
.content-details p {
  font-size: 0.8em;
  margin-top: 0;
  color: black;
}
